/*-------------------------
	The clocks
--------------------------*/

#clock {
  width: 300px;
  padding: 20px 10px 20px 10px;
  /* position: relative; */
}

#clock:after {
  content: "";
  margin-left: -100px;
  z-index: -1;
}

#clock .display {
  text-align: center;
  padding-top: 30px;
  border-radius: 6px;
  position: relative;
  height: 55px;
}

/*-------------------------
	Light color theme
--------------------------*/

#clock.light {
  background-color: #f3f3f3;
  color: #272e38;
}

#clock.light:after {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

#clock.light .digits div span {
  background-color: #272e38;
  border-color: #272e38;
}

#clock.light .digits div.dots:before,
#clock.light .digits div.dots:after {
  background-color: #272e38;
}

#clock.light .display {
  background-color: #dddddd;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.08) inset, 0 1px 1px #fafafa;
}

/*-------------------------
	Dark color theme
--------------------------*/

#clock.dark {
  background-color: #272e38;
  color: #cacaca;
}

#clock.dark:after {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

#clock.dark .digits div span {
  background-color: #cacaca;
  border-color: #cacaca;
}

#clock.dark .display {
  background-color: #0f1620;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.08) inset, 0 1px 1px #2d3642;
}

#clock.dark .digits div.dots:before,
#clock.dark .digits div.dots:after {
  background-color: #cacaca;
}

/*-------------------------
	The Digits
--------------------------*/

#clock .digits div {
  text-align: left;
  position: relative;
  width: 28px;
  height: 50px;
  display: inline-block;
  margin: 0 4px;
}

#clock .digits div span {
  opacity: 0;
  position: absolute;

  -webkit-transition: 0.25s;
  -moz-transition: 0.25s;
  transition: 0.25s;
}

#clock .digits div span:before,
#clock .digits div span:after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: 5px solid transparent;
}

#clock .digits .d1 {
  height: 5px;
  width: 10px;
  top: 0;
  left: 6px;
}
#clock .digits .d1:before {
  border-width: 0 5px 5px 0;
  border-right-color: inherit;
  left: -5px;
}
#clock .digits .d1:after {
  border-width: 0 0 5px 5px;
  border-left-color: inherit;
  right: -5px;
}

#clock .digits .d2 {
  height: 5px;
  width: 10px;
  top: 18px;
  left: 6px;
}
#clock .digits .d2:before {
  border-width: 3px 4px 2px;
  border-right-color: inherit;
  left: -8px;
}
#clock .digits .d2:after {
  border-width: 3px 4px 2px;
  border-left-color: inherit;
  right: -8px;
}

#clock .digits .d3 {
  height: 5px;
  width: 10px;
  top: 36px;
  left: 6px;
}
#clock .digits .d3:before {
  border-width: 5px 5px 0 0;
  border-right-color: inherit;
  left: -5px;
}
#clock .digits .d3:after {
  border-width: 5px 0 0 5px;
  border-left-color: inherit;
  right: -5px;
}

#clock .digits .d4 {
  width: 5px;
  height: 8px;
  top: 7px;
  left: 0;
}
#clock .digits .d4:before {
  border-width: 0 5px 5px 0;
  border-bottom-color: inherit;
  top: -5px;
}
#clock .digits .d4:after {
  border-width: 0 0 5px 5px;
  border-left-color: inherit;
  bottom: -5px;
}

#clock .digits .d5 {
  width: 5px;
  height: 8px;
  top: 7px;
  right: 6px;
}
#clock .digits .d5:before {
  border-width: 0 0 5px 5px;
  border-bottom-color: inherit;
  top: -5px;
}
#clock .digits .d5:after {
  border-width: 5px 0 0 5px;
  border-top-color: inherit;
  bottom: -5px;
}

#clock .digits .d6 {
  width: 5px;
  height: 8px;
  top: 26px;
  left: 0;
}
#clock .digits .d6:before {
  border-width: 0 5px 5px 0;
  border-bottom-color: inherit;
  top: -5px;
}
#clock .digits .d6:after {
  border-width: 0 0 5px 5px;
  border-left-color: inherit;
  bottom: -5px;
}

#clock .digits .d7 {
  width: 5px;
  height: 8px;
  top: 26px;
  right: 6px;
}
#clock .digits .d7:before {
  border-width: 0 0 5px 5px;
  border-bottom-color: inherit;
  top: -5px;
}
#clock .digits .d7:after {
  border-width: 5px 0 0 5px;
  border-top-color: inherit;
  bottom: -5px;
}

/* 1 */

#clock .digits div.one .d5,
#clock .digits div.one .d7 {
  opacity: 1;
}

