/* The dots */

#clock .digits div.dots {
  width: 5px;
}

#clock .digits div.dots:before,
#clock .digits div.dots:after {
  width: 5px;
  height: 5px;
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
}

#clock .digits div.dots:after {
  top: 28px;
}

/*-------------------------
	Weekdays
--------------------------*/

#clock .weekdays {
  font-size: 10px;
  position: absolute;
  width: 100%;
  top: 6px;
  left: 0;
  text-align: center;
}

#clock .weekdays span {
  opacity: 0.2;
  padding: 0 10px;
}

#clock .weekdays span.active {
  opacity: 1;
}
